import React from "react";
import { Grid, makeStyles, Typography, Box } from "@material-ui/core";
import { motion } from "framer-motion";
import "./index.scss";
import QuiltedImageList from "../ImageList";
import Location from "../Location";

function Hero() {
  const classes = useStyle();

  return (
    <Box className="container" data-observe id="home">
      <Grid className="subContainer" container alignItems="center">
        <Grid
          item
          xs={12}
          sm={12}
          md={6}
          style={{
            justifyContent: "center",
            display: "flex",
          }}
        >
          <div className={classes.imageWrapper}>
            <QuiltedImageList />
          </div>
        </Grid>

        <Grid item xs={12} sm={12} md={6} style={{ position: "relative" }}>
          <Typography variant="h4">Bienvenu,</Typography>

          <Typography
            variant="h3"
            style={{ minHeight: 60, fontFamily: "Caveat" }}
            color="primary"
            id="myElement"
          />

          <Location />

          <motion.div
            initial={{ rotate: 20, scale: 0 }}
            animate={{ rotate: 0, scale: 1 }}
            transition={{
              type: "spring",
              damping: 20,
              stiffness: 260,
            }}
            className={classes.buttonWrapper}
          ></motion.div>
        </Grid>
      </Grid>
    </Box>
  );
}

const useStyle = makeStyles((theme) => {
  return {
    imageWrapper: {
      width: 350,
      [theme.breakpoints.down("xs")]: {
        maxWidth: 325,
      },
      height: 450,
      background: theme.gradientColor,
      position: "relative",
    },
    image: {
      width: "100%",
      height: "auto",
      objectFit: "cover",
    },
    buttonWrapper: {
      marginTop: theme.spacing(2),
      display: "flex",
      gap: theme.spacing(3),
      flexWrap: "wrap",
      [theme.breakpoints.down("sm")]: {
        marginTop: theme.spacing(3),
      },
      [theme.breakpoints.only("sm")]: { justifyContent: "center" },
    },
  };
});

export default Hero;
