import React, { useState } from "react";
import { Formik, Form } from "formik";
import { useDispatch } from "react-redux";
import { setCulture } from "../../redux/actions/actionCulture";
import { getWeather } from "../../redux/actions/actionMeteo";
import Select from "../Select";
import Button from "../Button";
import cultures from "../../data";
import coordonnees from "../../data/coordonnees";
import { FORM_VALIDATION, INITIAL_FORM_STATE } from "./validation";

function Formulaire() {
  const dispatch = useDispatch();
  const [regionS, setRegionS] = useState("");

  const mapRegions = coordonnees.map((province) => {
    return province.regions.map((region) => region);
  });

  const regions = mapRegions[0].concat(mapRegions[1]);

  const handleSubmit = async (values, actions) => {
    const culture = cultures.filter((data) => data.id === values.culture);
    dispatch(await setCulture(culture[0]));
    const region = regions.filter((region) => region.id === values.region);
    if (regionS !== values.region) {
      dispatch(await getWeather(region[0].coord.lat, region[0].coord.long));
      setRegionS(values.region);
    }
  };

  return (
    <Formik
      initialValues={{ ...INITIAL_FORM_STATE }}
      validationSchema={FORM_VALIDATION}
      onSubmit={handleSubmit}
    >
      <Form
        style={{
          display: "flex",
          flexDirection: "column",
          gap: 8,
          paddingTop: 24,
        }}
      >
        <Select
          required
          label="Sélectioner la region"
          name="region"
          options={regions}
        />
        <Select
          required
          label="Sélectioner la culture"
          name="culture"
          options={cultures}
        />
        <Button>Chercher</Button>
      </Form>
    </Formik>
  );
}

export default Formulaire;
