import * as React from "react";
import { ImageList, ImageListItem } from "@material-ui/core";
import Dialog from "../Dialog";

function srcset(image, size, rows = 1, cols = 1) {
  return {
    src: `${image}?w=${size * cols}&h=${size * rows}&fit=crop&auto=format`,
    srcSet: `${image}?w=${size * cols}&h=${
      size * rows
    }&fit=crop&auto=format&dpr=2 2x`,
  };
}

export default function QuiltedImageList({ images, withModal = false }) {
  const [state, setstate] = React.useState({ img: null });
  const [open, setOpnen] = React.useState(false);
  return (
    <>
      <ImageList
        sx={{ width: "100%", height: "100%" }}
        variant="quilted"
        cols={4}
        rowHeight={121}
      >
        {images === undefined
          ? itemData.map((item) => (
              <ImageListItem
                key={item.img}
                cols={item.cols || 1}
                rows={item.rows || 1}
              >
                <img
                  {...srcset(item.img, 121, item.rows, item.cols)}
                  alt={item.title}
                  loading="lazy"
                />
              </ImageListItem>
            ))
          : withModal &&
            images.map((item) => (
              <ImageListItem
                key={item}
                cols={1}
                rows={1}
                onClick={() => {
                  setstate({ img: item });
                  setOpnen(true);
                }}
              >
                <img {...srcset(item, 121)} alt={item} loading="lazy" />
              </ImageListItem>
            ))}
      </ImageList>
      {withModal && (
        <Dialog openPopUp={open} setOpenPopUp={setOpnen}>
          <img
            style={{ width: "100%", height: "auto", objectFit: "cover" }}
            src={state.img}
            alt="Dialog"
            loading="lazy"
          />
        </Dialog>
      )}
    </>
  );
}

const itemData = [
  {
    img: "./img/choux.jpg",
    title: "choux",
    cols: 2,
  },
  {
    img: "./img/plante.jpg",
    title: "plante",
    cols: 2,
  },
  {
    img: "./img/vary1.jpg",
    title: "vary",
  },
  {
    img: "./img/mangahazo.jpg",
    title: "mangahazo",
    cols: 3,
  },
  {
    img: "./img/voanjo1.jpg",
    title: "voanjo",
    rows: 2,
    cols: 2,
  },
  {
    img: "./img/salady3.jpg",
    title: "salady",
    cols: 2,
  },
];
