import React from "react";
import { Box, Paper, Tooltip } from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";
import { Brightness4Outlined, BrightnessLowOutlined } from "@material-ui/icons";
import "./switch.scss";

function Switch({ switched }) {
  const classes = useStyle();
  return (
    <Tooltip title="Thème Switcher">
      <Box className="switch-button">
        <div className="button-wrapper">
          <Brightness4Outlined color="secondary" />
          <BrightnessLowOutlined color="secondary" />
          <Paper
            className={[switched ? "ball dark" : "ball light", classes.root]}
          />
        </div>
      </Box>
    </Tooltip>
  );
}

const useStyle = makeStyles((theme) => {
  return {
    root: {
      background: theme.gradientColor,
    },
  };
});

export default Switch;
