import React, { useEffect, useState } from "react";
import { Grid, makeStyles, Typography, Box as Mbox } from "@material-ui/core";
import { useSelector } from "react-redux";
import Box from "../Box";
import Formulaire from "../Formulaire";
import CropList from "../CropList";

function Start() {
  const classes = useStyle();
  const { value, images, info } = useSelector((state) => state.cultureReducer);
  const [culture, setCulture] = useState({
    value: null,
    info: null,
    images: [],
  });

  useEffect(() => {
    setCulture({ value, info, images });
  }, [value, images, info]);

  return (
    <Mbox className={classes.box} data-observe id="start">
      <Grid container className={classes.boxGrid}>
        <Grid
          item
          xs={12}
          sm={12}
          md={5}
          xl={5}
          className={classes.gridCardWrapper}
        >
          <Box title="getting started">
            <Typography variant="h4" gutterBottom={1}>
              Calendrier des cultures
            </Typography>
            <Typography variant="overline" gutterBottom={1}>
              Selectioner une region et une culture
            </Typography>
            <Formulaire />
          </Box>
        </Grid>
        <Grid item md={7} xl={7} style={{ width: "100%" }}>
          {culture.value && (
            <div style={{ padding: 4 }}>
              <Typography variant="h5">
                Culture de {culture.value.toLowerCase()}
              </Typography>
              <Typography variant="body1">{culture.info}</Typography>
              <div
                style={{
                  display: "flex",
                  flexWrap: "wrap",
                  gap: 8,
                }}
              ></div>
            </div>
          )}
          <CropList />
        </Grid>
      </Grid>
    </Mbox>
  );
}

const useStyle = makeStyles((theme) => {
  return {
    box: {
      zIndex: 1,
      paddingTop: theme.spacing(2),
      minHeight: "50vh",
      [theme.breakpoints.down("xs")]: {
        paddingTop: theme.spacing(4),
      },
      "& .cardWrapper": {
        display: "flex",
        gap: theme.spacing(2),
        marginTop: theme.spacing(5),
        flexWrap: "wrap",
        [theme.breakpoints.down("xs")]: {
          alignItems: "flex-start",
        },
        [theme.breakpoints.up("md")]: {
          marginTop: theme.spacing(2),
        },
      },
    },
    /*  boxGrid: {
      [theme.breakpoints.up("md")]: {
        alignItems: "center",
      },
    }, */
    gridCardWrapper: {
      display: "grid",
      justifyContent: "center",
      paddingRight: theme.spacing(8),
      [theme.breakpoints.down("sm")]: {
        justifyContent: "center",
      },
    },
    showXl: {
      [theme.breakpoints.down("md")]: {
        display: "none",
      },
    },
    image: {
      width: "100%",
      height: "auto",
      objectFit: "cover",
    },
  };
});

export default Start;
