import React, { useEffect, useState } from "react";
import { LocationOn } from "@material-ui/icons";
import { Typography } from "@material-ui/core";
import { useSelector } from "react-redux";

function Location() {
  const meteo = useSelector((state) => state.meteoReducer);

  const [location, setLocation] = useState({
    city: null,
    lat: null,
    long: null,
    icon: null,
    description: null,
    celsus: null,
    farhenaith: null,
  });

  useEffect(() => {
    if (meteo && meteo.current)
      setLocation({
        city: meteo.timezone,
        lat: meteo.lat,
        long: meteo.lon,
        icon: meteo.current.weather[0].icon,
        description: meteo.current.weather[0].description,
        celsus: Math.round(meteo.current.temp),
        farhenaith: null,
      });
  }, [meteo]);

  return (
    <div>
      <p
        style={{
          display: "flex",
          alignItems: "end",
        }}
      >
        <LocationOn />
        {location.city}
      </p>
      <div
        style={{
          display: "flex",
          alignItems: "end",
        }}
      >
        <Typography component="span">{location.celsus}°C</Typography>
        <img
          src={`https://openweathermap.org/img/w/${location.icon}.png`}
          alt="weather"
        />
      </div>
      <Typography component="span">{location.description}</Typography>
    </div>
  );
}

export default Location;
