import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import {
  AppBar,
  Box,
  Slide,
  Toolbar,
  Typography,
  useScrollTrigger,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";
import Switch from "../Switch";
import { darkMode, lightMode } from "../../redux/actions/actionTheme";
import { useDispatch } from "react-redux";
import LinkWrapper from "./LinkWrapper";
import "./navBar.scss";

function Header() {
  const classes = useStyles();
  const [switchOn, setSwitchOn] = useState(false);
  const history = useHistory();
  const dispatch = useDispatch();

  const trigger = useScrollTrigger({
    threshold: 70,
  });

  const switchTheme = () => {
    setSwitchOn(!switchOn);
    if (switchOn) dispatch(lightMode());
    else dispatch(darkMode());
  };

  return (
    <>
      <Slide appear={true} direction="down" in={!trigger}>
        <AppBar position="fixed" className={classes.root}>
          <Toolbar>
            <div className="appBar-wrapper">
              <Typography
                variant="h6"
                component="span"
                onClick={() => history.push({ pathname: "/" })}
                className={classes.brand}
              >
                CropCldR.
              </Typography>
            </div>
            <LinkWrapper />
            <Box onClick={switchTheme}>
              <Switch switched={switchOn} />
            </Box>
          </Toolbar>
        </AppBar>
      </Slide>
    </>
  );
}

const useStyles = makeStyles((theme) => {
  return {
    brand: {
      cursor: "pointer",
      color: theme.palette.primary.main,
      fontFamily: "Caveat",
    },
    root: {
      transition: "top 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms",
      zIndex: 1100,
      boxShadow: theme.boxShadow,
      backdropFilter: "blur(20px)",
      backgroundColor: theme.palette.background.default,
      "&::before": {
        height: 64,
        width: "100%",
        background: theme.palette.background.default,
        position: "absolute",
        top: 0,
        left: 0,
        right: 0,
        content: "''",
        filter: "blur(20px)",
        zIndex: -1,
      },
    },
  };
});

export default Header;
