import axios from "axios";

export const GET_WEATHER = "GET_WEATHER";
export const SET_WEATHER = "SET_WEATHER";
export const ERR_WEATHER = "ERR_WEATHER";

export const getWeather = async (latitude, longitude) => {
  return async (dispatch) => {
    await axios
      .get(
        `${process.env.REACT_APP_WEATHER_API_BASE_URL}?lat=${latitude}&lon=${longitude}&exclude=hourly,minutely,alerts&appid=${process.env.REACT_APP_WEATHER_API_KEY}&units=metric&lang=fr`
      )
      .then((res) =>
        dispatch({ type: GET_WEATHER, payload: { error: false, ...res.data } })
      )
      .catch((err) =>
        dispatch({
          type: ERR_WEATHER,
          payload: { message: err.message, error: true },
        })
      );
  };
};

export const setWeather = async () => {
  return async (dispatch) => {
    await axios
      .get("http://localhost:3000/meteo.json")
      .then((res) => dispatch({ type: SET_WEATHER, payload: res.data }))
      .catch((err) => console.log(err));
  };
};
