import {
  GET_WEATHER,
  SET_WEATHER,
  ERR_WEATHER,
} from "../../actions/actionMeteo";

const initialState = {};

function meteoReducer(state = initialState, action) {
  switch (action.type) {
    case GET_WEATHER:
      return action.payload;
    case ERR_WEATHER:
      return action.payload;
    case SET_WEATHER:
      return action.payload;
    default:
      return state;
  }
}

export default meteoReducer;
