import React, { useRef } from "react";
import { Typography, makeStyles } from "@material-ui/core";
import { Skeleton } from "@material-ui/lab";
import { useSelector } from "react-redux";
import { motion } from "framer-motion";
import CropCard from "../CropCard";

function CropList() {
  const ref = useRef();
  const meteo = useSelector((state) => state.meteoReducer);
  const culture = useSelector((state) => state.cultureReducer);
  const classes = useStyle();

  return (
    <>
      {meteo.daily && culture.humidity ? (
        <div>
          <Typography variant="h6" style={{ padding: "5px 0" }}>
            Calendrier culturel dans cette semaine
          </Typography>
          <div className={classes.legend}>
            <p>
              <span className={classes.si} /> autoriser
            </p>
            /
            <p>
              <span className={classes.warning} /> autoriser(non recommender)
            </p>
            /
            <p>
              <span className={classes.non} /> non autoriser
            </p>
          </div>
          <div ref={ref} className={classes.overflow}>
            <motion.div
              style={{
                display: "flex",
                gap: 15,
                padding: "5px",
                width: 1715,
              }}
              drag="x"
              dragConstraints={ref}
            >
              {meteo.daily.map((daily, index) => (
                <CropCard
                  humidity={daily.humidity}
                  tempMin={daily.temp.min}
                  tempMax={daily.temp.max}
                  timestamp={daily.dt}
                  sunrise={daily.sunrise}
                  sunset={daily.sunset}
                  icon={daily.weather[0].icon}
                  description={daily.weather[0].description}
                  index={index}
                />
              ))}
            </motion.div>
          </div>
        </div>
      ) : (
        culture.humidity &&
        meteo.error && (
          <div style={{ display: "flex", gap: 10, flexWrap: "wrap" }}>
            {[0, 1, 2].map(() => (
              <div>
                <Skeleton variant="rect" width={200} height={100} />
                <Skeleton variant="text" width={150} />
                <Skeleton variant="text" width={75} />
              </div>
            ))}
          </div>
        )
      )}
    </>
  );
}

const useStyle = makeStyles((theme) => ({
  overflow: {
    overflow: "hidden",
  },
  non: {
    display: "inline-block",
    backgroundColor: "red",
    width: 10,
    height: 10,
    borderRadius: 25,
  },
  si: {
    display: "inline-block",
    backgroundColor: "green",
    width: 10,
    height: 10,
    borderRadius: 25,
  },
  warning: {
    display: "inline-block",
    backgroundColor: "orange",
    width: 10,
    height: 10,
    borderRadius: 25,
  },
  legend: {
    padding: "0px 5px",
    display: "flex",
    flexWrap: "wrap",
    gap: 20,
    "& p": {
      display: "flex",
      alignItems: "center",
      gap: 10,
    },
  },
}));

export default CropList;
