import React from "react";
import { Grid, makeStyles, Typography, Box as Mbox } from "@material-ui/core";
import Box from "../Box";
import { images } from "../../data";
import QuiltedImageList from "../ImageList";

function Gallery() {
  const classes = useStyle();
  return (
    <Mbox className={classes.box} data-observe id="gallery">
      <Grid container className={classes.boxGrid}>
        <Grid
          item
          xs={12}
          sm={12}
          md={12}
          xl={12}
          className={classes.gridCardWrapper}
        >
          <Box title="la gallerie">
            <Typography variant="h4" gutterBottom={1}>
              Quelque photo de gallerie
            </Typography>
            <Typography variant="overline" gutterBottom={1}>
              Jeter un coup d'oeil
            </Typography>

            <QuiltedImageList images={images} withModal />
          </Box>
        </Grid>
      </Grid>
    </Mbox>
  );
}

const useStyle = makeStyles((theme) => {
  return {
    box: {
      zIndex: 1,
      paddingTop: theme.spacing(2),
      minHeight: "100vh",
      [theme.breakpoints.down("xs")]: {
        paddingTop: theme.spacing(4),
      },
      "& .cardWrapper": {
        display: "flex",
        gap: theme.spacing(2),
        marginTop: theme.spacing(5),
        flexWrap: "wrap",
        [theme.breakpoints.down("xs")]: {
          alignItems: "center",
        },
        [theme.breakpoints.up("md")]: {
          marginTop: theme.spacing(2),
        },
      },
    },
    boxGrid: {
      [theme.breakpoints.up("md")]: {
        alignItems: "flex-start",
      },
    },
    gridCardWrapper: {
      display: "grid",
      justifyContent: "center",
      paddingRight: theme.spacing(8),
      [theme.breakpoints.down("sm")]: {
        justifyContent: "center",
      },
    },
    image: {
      width: "100%",
      height: "auto",
      objectFit: "cover",
    },
  };
});

export default Gallery;
