import React, { useEffect, useRef, useState } from "react";
import { Link as Mlink } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

function Link({ name }) {
  const classes = useStyle();
  const linkRef = useRef();
  const [isActive, setisActive] = useState(false);
  const ratio = 0.6;
  let observer;

  /**
   * Active le lien par rapport aux element à abserver
   * @param {HTMLElement} element
   */
  const activate = (element) => {
    let active = element.getAttribute("id");

    if (linkRef.current.hash.includes(active)) {
      setisActive(true);
    } else {
      setisActive(false);
    }
  };

  /**
   * Callback de l'objet IntersectionObserver
   * @param {IntersectionObserverEntry} entries
   * @param {IntersectionObserver} observer
   */
  const callback = function (entries, observer) {
    entries.forEach(function (entry) {
      if (entry.isIntersecting && entry.intersectionRatio > 0) {
        activate(entry.target);
      }
    });
  };

  /**
   * Observe les elements
   * @param {NodeListOf.<Element>} spies
   */
  const observe = (spies) => {
    if (observer !== undefined) {
      spies.forEach((spy) => observer.unobserve(spy));
    }
    const bottom = Math.round(window.innerHeight * ratio);
    const top = Math.round(window.innerHeight - bottom - 1);
    observer = new IntersectionObserver(callback, {
      rootMargin: `-${top}px 0px -${bottom}px 0px`,
    });
    spies.forEach((spy) => observer.observe(spy));
  };

  useEffect(() => {
    let spies = document.querySelectorAll("[data-observe]");
    if (spies.length > 0) {
      observe(spies);
      window.addEventListener("resize", () => {
        observe(spies);
      });
    }
  });

  return (
    <Mlink
      className={[classes.link, isActive && "active"]}
      href={`#${name}`}
      underline="none"
      ref={linkRef}
    >
      {name}
    </Mlink>
  );
}

const useStyle = makeStyles((theme) => ({
  link: {
    textTransform: "capitalize",
    color: theme.palette.primary.main,
    position: "relative",
    "&.active::before": {
      height: 2,
      width: "100%",
      background: theme.switch,
      bottom: 0,
      borderRadius: 8,
      position: "absolute",
      content: "''",
    },
  },
}));

export default Link;
