export const getPosition = () => {
  return new Promise(function (resolve, reject) {
    navigator.geolocation.watchPosition(resolve, reject);
  });
};

/**
 * recuperer un date
 * @param {Number} timestamp
 * @returns {String}
 */
export const date = (timestamp) => {
  const date = new Date(timestamp * 1000);
  const jour = date.getDate();
  const nbMois = date.getMonth();
  let mois;
  switch (nbMois) {
    case 0:
      mois = "Jan";
      break;
    case 1:
      mois = "Fev";
      break;
    case 2:
      mois = "Mar";
      break;

    case 3:
      mois = "Avr";
      break;

    case 4:
      mois = "Mai";
      break;

    case 5:
      mois = "Juin";
      break;

    case 6:
      mois = "Juil";
      break;

    case 7:
      mois = "Aou";
      break;

    case 8:
      mois = "Sept";
      break;

    case 9:
      mois = "Oct";
      break;

    case 10:
      mois = "Nov";
      break;
    case 11:
      mois = "Dec";
      break;
    default:
      mois = "";
      break;
  }
  return `${jour} ${mois}`;
};

/**
 * recuperer l'heure
 * @param {Number} timestamp
 * @returns {String}
 */
export const heure = (timestamp) => {
  const date = new Date(timestamp * 1000);
  const heure = date.getHours();
  const minute = date.getMinutes();

  return `${heure}h : ${minute}min`;
};
