import React, { useState } from "react";
import {
  Button,
  Card,
  CardContent,
  CardHeader,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  IconButton,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";
import { MoreVert } from "@material-ui/icons";
import { date, heure } from "../../utils";
import { useSelector } from "react-redux";

const subheader = (index, timestamp) => {
  return (
    (index === 0 && "Aujourd'hui") ||
    (index === 1 && "Demain") ||
    (index === 2 && "Après demain") ||
    (index > 2 && date(timestamp))
  );
};
function CropCard({
  humidity,
  tempMin,
  tempMax,
  timestamp,
  sunrise,
  sunset,
  icon,
  description,
  index,
}) {
  const classes = useStyles();
  const culture = useSelector((state) => state.cultureReducer);
  const [show, setShow] = useState(false);

  return (
    <>
      <Card variant="outlined" style={{ minWidth: 200 }}>
        <CardHeader
          subheader={subheader(index, timestamp)}
          action={
            <IconButton aria-label="more" onClick={() => setShow(true)}>
              <MoreVert />
            </IconButton>
          }
        />
        <CardContent>
          <p>
            Recolte : {""}
            <span
              className={
                (humidity < culture.humidity.min && classes.non) ||
                (humidity < culture.humidity.max && classes.si) ||
                (humidity >= culture.humidity.max && classes.warning)
              }
            ></span>
          </p>
          <p>
            Plantation : {""}
            <span
              className={
                (tempMin > culture.temperature.min &&
                  tempMax < culture.temperature.max &&
                  classes.si) ||
                (tempMin < culture.temperature.min &&
                  tempMax > culture.temperature.max &&
                  classes.non) ||
                (((tempMin < culture.temperature.min &&
                  tempMax < culture.temperature.max) ||
                  (tempMin > culture.temperature.min &&
                    tempMax > culture.temperature.max)) &&
                  classes.warning)
              }
            ></span>
          </p>
        </CardContent>
      </Card>

      <Dialog open={show} onClose={() => setShow(false)}>
        <DialogTitle>
          Calendrier culturel pour{" "}
          {subheader(index, timestamp).toLocaleLowerCase()}
        </DialogTitle>
        <DialogContent>
          <div
            style={{
              display: "flex",
              justifyContent: "space-between",
              alignItems: "flex-end",
              position: "relative",
            }}
          >
            <div>
              <p>
                Recolte : {""}
                <span
                  className={
                    (humidity < culture.humidity.min && classes.non) ||
                    (humidity < culture.humidity.max && classes.si) ||
                    (humidity >= culture.humidity.max && classes.warning)
                  }
                />
              </p>
              <p>
                Plantation : {""}
                <span
                  className={
                    (tempMin > culture.temperature.min &&
                      tempMax < culture.temperature.max &&
                      classes.si) ||
                    (tempMin < culture.temperature.min &&
                      tempMax > culture.temperature.max &&
                      classes.non) ||
                    (((tempMin < culture.temperature.min &&
                      tempMax < culture.temperature.max) ||
                      (tempMin > culture.temperature.min &&
                        tempMax > culture.temperature.max)) &&
                      classes.warning)
                  }
                />
              </p>
            </div>
            <span style={{ position: "absolute", top: 0, right: 75 }}>
              {Math.round(tempMax)} °
            </span>
            <span
              style={{ position: "absolute", top: 6, right: 50, fontSize: 28 }}
            >
              C
            </span>
            <span style={{ position: "absolute", bottom: 0, right: 75 }}>
              {Math.round(tempMin)} °
            </span>
            <img
              src={`https://openweathermap.org/img/w/${icon}.png`}
              alt="meteo"
            />
          </div>
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              paddingTop: 25,
            }}
          >
            <p> {description} </p>
            <p>Humidité: {humidity}%</p>
            <p>Lever du soleil: {heure(sunrise)}</p>
            <p> Coucher du soleil: {heure(sunset)}</p>
          </div>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setShow(false)}>Fermer</Button>
        </DialogActions>
      </Dialog>
    </>
  );
}

const useStyles = makeStyles((theme) => {
  return {
    non: {
      display: "inline-block",
      backgroundColor: "red",
      width: 10,
      height: 10,
      borderRadius: 25,
    },
    si: {
      display: "inline-block",
      backgroundColor: "green",
      width: 10,
      height: 10,
      borderRadius: 25,
    },
    warning: {
      display: "inline-block",
      backgroundColor: "orange",
      width: 10,
      height: 10,
      borderRadius: 25,
    },
  };
});

export default CropCard;
